body {
    background-color: rgba(0, 0, 0, 0.6);
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
  
  .to-do-list {
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
    margin-top: 100px;
  }
  
  h1 {
    font-size: 4rem;
    color: white;
  }
  
  button {
    font-size: 1.7rem;
    font-weight: bold;
    padding: 10px 20px;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.5s ease;
  }
  .chattArea{
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    padding: 10px;
    font-family:Arial, Helvetica, sans-serif;
    font-weight: 900;
    font-size: 1.5rem;
    color: rgb(255, 255, 255);
    border-radius: 50%;
  }




  .formWrap{
    display: flex;
    padding: 20px;
justify-content: center;
  }

  .add-button {
    background-color: rgba(29, 203, 76, 1);
  }
  
  .add-button:hover {
    background-color: rgba(29, 203, 76, 0.7);
  }
  
  .delete-button {
    background-color: rgba(223, 26, 26, 1);
  }
  
  .delete-button:hover {
    background-color: rgba(223, 26, 26, 0.7)
  }
  
  .move-button {
    background-color: rgba(49, 92, 230, 1);
  }
  
  .move-button:hover {
    background-color: rgba(49, 92, 230, 0.7);
  }
  
  input[type="text"] {
    font-size: 1.6rem;
    padding: 10px;
    border: 2px solid rgb(7, 7, 7);
    border-radius: 5px;
    color: black;
  }
  
  ol {
    padding: 0;
  }
  
  li {
    display: flex;
    align-items: center;
    font-size: 2rem;
    font-weight: bold;
    padding: 15px;
    background-color: white;
    margin-bottom: 10px;
    border: 3px solid rgb(232, 232, 191);
    border-radius: 5px;
  }
  
  .text {
    flex: 1;
    max-width: 50%;
  }
  
  .delete-button,
  .move-button {
    padding: 8px 12px;
    font-size: 1.4rem;
    margin-left: 10px;
  
  }